.capabilities-container {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.capabilities-container .capabilities-header {
  margin-bottom: 1.5rem;
}
.capabilities-container .capabilities-header .capabilities-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 0.5rem 0;
}
.capabilities-container .capabilities-header .capabilities-description {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
}
.capabilities-container .capabilities-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
.capabilities-container .capabilities-grid .capability-card {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 1rem;
  border: 1px solid #e5e7eb;
  border-radius: 0.75rem;
  transition: all 0.2s ease;
  background: #f8fafc;
}
.capabilities-container .capabilities-grid .capability-card:hover {
  border-color: #3b82f6;
  background: white;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.1);
  transform: translateY(-1px);
}
.capabilities-container .capabilities-grid .capability-card .capability-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 0.75rem;
  flex-shrink: 0;
}
.capabilities-container .capabilities-grid .capability-card .capability-icon.icon-blue {
  background: #dbeafe;
  color: #1e40af;
}
.capabilities-container .capabilities-grid .capability-card .capability-icon.icon-green {
  background: #dcfce7;
  color: #166534;
}
.capabilities-container .capabilities-grid .capability-card .capability-icon.icon-purple {
  background: #f3e8ff;
  color: #7c3aed;
}
.capabilities-container .capabilities-grid .capability-card .capability-icon.icon-orange {
  background: #fed7aa;
  color: #c2410c;
}
.capabilities-container .capabilities-grid .capability-card .capability-icon.icon-red {
  background: #fecaca;
  color: #dc2626;
}
.capabilities-container .capabilities-grid .capability-card .capability-icon.icon-indigo {
  background: #e0e7ff;
  color: #4338ca;
}
.capabilities-container .capabilities-grid .capability-card .capability-content {
  flex: 1;
  min-width: 0;
}
.capabilities-container .capabilities-grid .capability-card .capability-content .capability-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 0.25rem 0;
}
.capabilities-container .capabilities-grid .capability-card .capability-content .capability-description {
  font-size: 0.75rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.4;
}
.capabilities-container .capabilities-grid .capability-card.capability-search {
  border-left: 3px solid #3b82f6;
}
.capabilities-container .capabilities-grid .capability-card.capability-financial {
  border-left: 3px solid #10b981;
}
.capabilities-container .capabilities-grid .capability-card.capability-maintenance {
  border-left: 3px solid #8b5cf6;
}
.capabilities-container .capabilities-grid .capability-card.capability-analytics {
  border-left: 3px solid #f59e0b;
}
.capabilities-container .capabilities-grid .capability-card.capability-reports {
  border-left: 3px solid #ef4444;
}
.capabilities-container .capabilities-grid .capability-card.capability-recommendations {
  border-left: 3px solid #6366f1;
}

@media (max-width: 768px) {
  .capabilities-container {
    padding: 1rem;
  }
  .capabilities-container .capabilities-grid .capability-card {
    padding: 0.75rem;
  }
  .capabilities-container .capabilities-grid .capability-card .capability-icon {
    width: 2rem;
    height: 2rem;
  }
}
